.card{
    width: 282px;
}
.card .imgbox{
    width: 100%;
    height: 210px;
}
.card .imgbox img{
    width: 100%;
    height: 100%;
}
.card .info{
    width: 100%;
}
.card .info .tit{
    width: 100%;
    color: #111;
    margin: 40px 0 16px;
}
.card .info .tit h4{
    font-size: 17px;
}
.card .info .tit p{
    font-size: 14px;
    color: #333;
    margin: 8px 0 16px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.card .info .tit h3{
    font-size: 22px;
}
.card .info .tit h3 span{
    font-size: 17px;
}
.card .info .sp{
    color: #666;
    font-size: 12px;
    padding: 4px 8px;
    border: 1px solid #666;
    margin-right: 10px;
}